import React, { useState } from 'react';
import './style.css'
import HomePage from './components/HomePage'
import Projects from './components/Projects'
function App() {
  const [tab, setTab] = useState('home')
  return (
    <div className="App">
      <nav>
        <ul id='nav'>
          <li className={`nav-item ${tab === 'home' ? 'pressed' : ''}`} onClick={() => { setTab('home') }}>About</li>
          <li className={`nav-item ${tab === 'projects' ? 'pressed' : ''}`} onClick={() => { setTab('projects') }}> Projects</li>
        </ul>
      </nav>
      <main className="my-container">
        {tab === 'home' ? <HomePage /> : <Projects />}
      </main>

    </div>
  );
}

export default App;
