import React, {Fragment} from 'react'
import ProjectDemo from './ProjectDemo/ProjectDemo'
import landingPageImage from '../media/landing-page.PNG'
import minecraftImage from '../media/minecraft.PNG'
import planImage from '../media/plan.PNG'

const Projects = () => {
    return (
        <Fragment>
        <div className='main-text'>
            <h1 className='header'>How may I be of help?</h1>
            <h2 className='sub-header'>I've worked with:</h2>
            Front-end: HTML, CSS, JS, Jquery, Bootstrap, Material-UI, React, Redux<br />Back-end: Python, Django,
            Django-rest. JavaScript, Node.js<br /> Databases: PostgreSQL, MySQl
           
        </div>

        <div className="main-projects">
        <h2 className='sub-header'>Some of my work:</h2>

        <ProjectDemo title='landing page' description='Basic HTML & CSS landing page' codeLink='https://github.com/sapirm290/Landing_page' siteLink='https://sapirm290.github.io/Landing_page/'  image={landingPageImage}/>
         <ProjectDemo title='mini minecraft' description='A simple minecraft like game using JS' codeLink='https://github.com/sapirm290/Minecraft' siteLink='https://sapirm290.github.io/Minecraft/' image={minecraftImage} />
         <ProjectDemo title='Todo app' description=' A full APP using React, Redux, Material UI for the front-end, and Python, Django, Django-rest, knox auth
         for the back-end. hosted on Heroku' codeLink='https://github.com/sapirm290/planner' siteLink='https://arcane-cliffs-92152.herokuapp.com/?#/' image={planImage} />
   
        </div>
       </Fragment>
    )
}

export default Projects
