import React from 'react'
import { createUseStyles } from 'react-jss'
import PropTypes from 'prop-types';


const useStyles = createUseStyles({
    demo: {
        padding: '5rem 1rem',
        backgroundColor: '#999',
        position: 'relative',
        borderBottom: [1, 'solid', '#E4E4E4'],

        '& img': {
            width: '100%',
            maxWidth: '1000px',
            opacity: 1,
            transition: '.5s ease',

        },


        '& div': {
            alignItems: 'center',
            display: 'flex',
            width: '100%',
            maxWidth: '650px',
            fontSize: '3rem',
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            '-ms-transform': 'translate(-50%, -50%)',
            textAlign: 'center',
            opacity: 0.1,
            color: '#000'


        },
        '& .description': {
            flexBasis: '70%'
        },
        '& .link': {
            paddingLeft: '1rem',
            paddingRight: '1rem'

        },
        '&:nth-child(even)': {
            backgroundColor: '#F7F7F7'
        },
        '&:nth-child(odd)': {
            backgroundColor: '#FFF'
        },

        '&:hover': {
            '& img': {
                opacity: 0.3,

            },
            '& div': {
                opacity: 1,
                color: '#333',
                transition: '.5s ease'

            },
            '& a': {
                opacity: 1,
                color: 'teal',
                transition: '.5s ease'

            }
        }


    },



})

const ProjectDemo = ({ title, description, codeLink, siteLink, image }) => {
    const classes = useStyles()
    return (
        <figure className={classes.demo}>
            <img src={image} alt={'sample ' + title} />
            <div >
                <p className='description'>{description}</p>
                <p ><a className='link' href={codeLink}>
                    Code</a> <a className='link' href={siteLink}> Site</a></p>
            </div>


        </figure>
    )
}
ProjectDemo.propTypes = {
    title: PropTypes.string,
    description: PropTypes.string,
    codeLink: PropTypes.string,
    siteLink: PropTypes.string,
    image: PropTypes.string
};
export default ProjectDemo
